.features__wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
  padding-top: 109px;
  box-sizing: border-box;
  color: $dark-grey;

  @media (max-width: 1023px) {
    padding: 54px 24px 0;
  }

  @media (max-width: 767px) {
    padding: 37px 16px 0 14px;
  }
}

.features__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
}

.features__item {
  width: 570px;
  min-height: 299px;
  margin-bottom: 30px;
  padding: 40px 42px 0;
  box-sizing: border-box;
  border: 1px solid #ebebeb;
  border-left: 3px solid $blue;
  cursor: pointer;

  @media (max-width: 1023px) {
    width: calc((100vw - 80px)/2);
    padding: 24px 0 0 25px;

    &:nth-child(2n-1) {
      margin-right: 30px;
    }
  }

  @media (max-width: 767px) {
    width: 100%;

    &:nth-child(2n-1) {
      margin-right: 0;
    }
  }

  &:hover {
    background-color: $blue;
  }
}

.features__item:hover h3,
.features__item:hover p{
  color: $white;
}

.features__item:hover svg {
  fill: $white
}

.features__item h3 {
  margin: 0;
  margin-bottom: 15px;
  padding-top: 17px;
  font-weight: normal;
  font-size: 24px;
  line-height: 34px;
  color: $blue;

  @media (max-width: 767px) {
    font-size: 20px;
    line-height: 28px;
  }
}

.features__item p {
  margin: 0;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
}
